@import url(https://fonts.googleapis.com/css?family=Bungee+Shade);
*,
:after,
:before {
  box-sizing: border-box;
}

:focus {
  outline: none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #77bfde;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
  max-width: 80%;
  margin: 0 auto;
  text-align: center;
  font-family: "Bungee Shade", sans-serif;
}

.FeedbackButton_btn__2D3gI {
  overflow: visible;
  border: 0;
  padding: 0;
  margin: 1.8rem;
}

.FeedbackButton_btn__2D3gI.FeedbackButton_stripedShadow__DLo_c span {
  display: block;
  position: relative;
  z-index: 2;
  border: 5px solid;
}
.FeedbackButton_btn__2D3gI.FeedbackButton_stripedShadow__DLo_c.FeedbackButton_white__2yw6J span {
  border-color: #fff;
  color: #fff;
  background: #77bfde;
}

.FeedbackButton_btn__2D3gI {
  font-family: "Bungee Shade", sans-serif;
  height: 80px;
  line-height: 65px;
  display: inline-block;
  letter-spacing: 1px;
  position: relative;
  font-size: 1.35rem;
  transition: opacity 0.3s, z-index 0.3s step-end, transform 0.3s;
  z-index: 1;
  background-color: transparent;
  cursor: pointer;
}

.FeedbackButton_btn__2D3gI {
  width: 155px;
  height: 48px;
  line-height: 38px;
}

button.FeedbackButton_btn__2D3gI.FeedbackButton_stripedShadow__DLo_c.FeedbackButton_white__2yw6J:after,
button.FeedbackButton_btn__2D3gI.FeedbackButton_stripedShadow__DLo_c.FeedbackButton_white__2yw6J:before {
  background-image: linear-gradient(
    135deg,
    transparent 0,
    transparent 5px,
    #fff 5px,
    #fff 10px,
    transparent 10px
  );
}

button.FeedbackButton_btn__2D3gI.FeedbackButton_stripedShadow__DLo_c:hover:before {
  max-height: calc(100% - 10px);
}

button.FeedbackButton_btn__2D3gI.FeedbackButton_stripedShadow__DLo_c:after {
  width: calc(100% - 4px);
  height: 8px;
  left: -10px;
  bottom: -9px;
  background-size: 15px 8px;
  background-repeat: repeat-x;
}
button.FeedbackButton_btn__2D3gI.FeedbackButton_stripedShadow__DLo_c:after,
button.FeedbackButton_btn__2D3gI.FeedbackButton_stripedShadow__DLo_c:before {
  content: "";
  display: block;
  position: absolute;
  z-index: 1;
  transition: transform 0.3s, max-height 0.3s, width 0.3s;
}

.FeedbackButton_btn__2D3gI.FeedbackButton_stripedShadow__DLo_c:hover {
  transform: translate(-12px, 12px);
  z-index: 3;
}

button.FeedbackButton_btn__2D3gI.FeedbackButton_stripedShadow__DLo_c:hover:after,
button.FeedbackButton_btn__2D3gI.FeedbackButton_stripedShadow__DLo_c:hover:before {
  transform: translate(12px, -12px);
}
button.FeedbackButton_btn__2D3gI.FeedbackButton_stripedShadow__DLo_c:before {
  width: 8px;
  max-height: calc(100% - 5px);
  height: 100%;
  left: -12px;
  bottom: -5px;
  background-size: 8px 15px;
  background-repeat: repeat-y;
  background-position: 0 100%;
}

ul {
  list-style: none;
  color: #ffffff;
}

